import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import xvue from '@/components/xvue.vue'
import HelloWorld from '@/components/HelloWorld.vue'

Vue.use(VueResource)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'xvue',
      component: xvue
    },
    {
      path: '/',
      name: 'HelloWorld', 
      component: HelloWorld 
    }
  ]
})



