<template>
	<div>
		<h1>123</h1>
		<p> {{ $store.state.a.count }} - {{ $store.state.count }} - {{count}}</p>
		<p> {{ $store.state }} </p>
		<button @click="$store.commit('jia',{a:10})">+</button>
		<button @click="jian">-</button>
<br>

		<button @click="jiaplus">+</button>

		<button @click="jianplus">-</button>
		<router-link to="/index">asdf</router-link>
	</div>
</template>
<script>
import { mapState,mapMutations,mapGetters,mapActions} from 'vuex'
	export default{
		name:'app',
		// data(){
		// 	return{
		// 		count :0
		// 	}
		// },
		computed:{

			// ...mapState([
			// 	'count'
			// 	]),

			// ...mapGetters([
			// 	"count"
			// 	])
			count(){
				return this.$store.state.a.count
			}

		},
		methods:{
			...mapMutations([
				"jia",
				"jian"
			]),
			...mapActions([
				"jiaplus"
				]),
			...mapActions(
			{
				jianplus:'jianplus'
			})
		}
		


		
	}
	document.title = "asdfasaasaas"
</script>


